/******************************************************************
  - MIXINS -
******************************************************************/
.latest_news_cards {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.latest_news_cards .latest_news_card {
  text-decoration: none;
  display: block;
  background: #fefefe;
}
.latest_news_cards .latest_news_card-image {
  position: relative;
  height: 240px;
}
.latest_news_cards .latest_news_card-image:after {
  content: " ";
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.latest_news_cards .latest_news_card-image img {
  width: 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.latest_news_cards .latest_news_card-inner {
  background: #fefefe;
  padding: 2rem 1.5rem;
  text-align: center;
}
.latest_news_cards .latest_news_card-inner h3 {
  margin-bottom: 1rem;
  text-decoration: none;
}
.latest_news_cards .latest_news_card-inner p {
  margin-bottom: 1.5rem;
  color: #0a0a0a;
  text-decoration: none;
}
.latest_news_cards .latest_news_card:hover .latest_news_card-image:after {
  background: rgba(0, 0, 0, 0.2);
}
.latest_news_cards .latest_news_card:hover h4 {
  color: #0a0a0a;
}
.latest_news_cards .latest_news_card:hover .button {
  background: #0a0a0a;
}

@media print, screen and (min-width: 40em) {
  .latest_news_cards .latest_news_card-image {
    height: 300px;
  }
  .latest_news_cards .latest_news_card-inner {
    padding: 2rem 1.5rem;
  }
  .latest_news_cards .latest_news_card-inner h3 {
    font-size: 2rem;
  }
}
@media print, screen and (min-width: 64em) {
  .latest_news_cards {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .latest_news_cards .latest_news_card-image {
    height: 300px;
  }
  .latest_news_cards .latest_news_card-inner {
    padding: 2rem 1.5rem;
  }
  .latest_news_cards .latest_news_card-inner p {
    font-size: 1.25rem;
  }
}
@media screen and (min-width: 75em) {
  .latest_news_cards .latest_news_card-image {
    height: 370px;
  }
}
@media print, screen and (max-width: 63.99875em) {
  .small-slides {
    margin: 0 1rem;
  }
}